/* Auth container */
body {
  margin: 0;
  padding: 0;
  background-color: #000;  /* full page black */
  font-family: 'Poppins', sans-serif;
}

.auth-container {
  width: 400px;
  margin: 80px auto;
  padding: 40px 32px;
  height: 600px;
  background: white;
  border-radius: 16px;
  box-shadow: 0 10px 35px rgba(0, 0, 0, 0.15);
  font-family: 'Poppins', sans-serif;
  text-align: center;
  animation: fadeInUp 0.5s ease;
}

.auth-container h2 {
  margin-bottom: 20px;
  font-size: 24px;
  color: #1a1a1a;
  font-weight: 600;
}

/* Auth form */
.auth-form {
  display: flex;
  flex-direction: column;
  gap: 14px;
  margin-bottom: 18px;
}

.auth-form input {
  padding: 12px 16px;
  border: 1px solid #ccc;
  border-radius: 10px;
  font-size: 14px;
  outline: none;
  transition: border 0.3s ease, box-shadow 0.3s ease;
}

.auth-form input:focus {
  border-color: #5a2e98;
  box-shadow: 0 0 6px rgba(90, 46, 152, 0.3);
}

/* Submit button */
.auth-form button {
  padding: 12px;
  background: linear-gradient(135deg, #4c1d95, #7e22ce);
  color: #fff;
  font-size: 15px;
  font-weight: 500;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.auth-form button:hover {
  background: linear-gradient(135deg, #5b21b6, #9333ea);
  transform: translateY(-2px);
  box-shadow: 0 6px 14px rgba(128, 90, 213, 0.35);
}

/* Toggle link */
.auth-toggle {
  font-size: 14px;
  color: #6b21a8;
  cursor: pointer;
  margin: 12px 0;
  transition: color 0.3s ease;
}

.auth-toggle:hover {
  color: #9333ea;
  text-decoration: underline;
}

/* Divider */
.auth-divider {
  font-size: 14px;
  color: #999;
  margin: 18px 0;
  position: relative;
}

.auth-divider::before,
.auth-divider::after {
  content: "";
  position: absolute;
  top: 50%;
  width: 40%;
  height: 1px;
  background: #ddd;
}

.auth-divider::before {
  left: 0;
}

.auth-divider::after {
  right: 0;
}

/* Social buttons */
.auth-social {
  width: 100%;
  padding: 12px;
  margin-bottom: 12px;
  border-radius: 10px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  border: none;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.auth-social.google {
  background: #0d062e;
  color: white;
  border: 1px solid #ccc;
}

.auth-social.google:hover {
  background: #160c82;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.08);
}

.auth-social.facebook {
  background: #1877f2;
  color: #fff;
}

.auth-social.facebook:hover {
  background: #07172b;
}

/* Error message */
.auth-error {
  color: #dc2626;
  font-size: 13px;
  margin-top: 8px;
  font-weight: 500;
}

/* Animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive */
@media (max-width: 480px) {
  .auth-container {
    width: 90%;
    padding: 28px 20px;
  }
}
